
'use client';

import { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Header } from '@/components/layout/Header';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Loader2, PenLine, Save } from 'lucide-react';
import type { Church } from '@/types';
import { useRouter } from 'next/navigation';
import AppShell from '@/components/layout/AppShell';
import Link from 'next/link';
import { doc, updateDoc } from 'firebase/firestore';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { useDoc } from '@/firebase/hooks/use-doc';
import { errorEmitter } from '@/firebase/error-emitter';
import { FirestorePermissionError } from '@/firebase/errors';

const settingsSchema = z.object({
  name: z.string().min(2, { message: 'Church name must be at least 2 characters.' }),
  address: z.string().optional(),
  phone: z.string().optional(),
  adminName: z.string().min(2, { message: 'Admin name must be at least 2 characters.' }),
  adminEmail: z.string().email({ message: 'Please enter a valid email address.' }),
});

type SettingsFormValues = z.infer<typeof settingsSchema>;

export default function SettingsPage() {
  const { churchId, isLoggedIn, isAdmin, user: appUser } = useAuth();
  const { firestore } = useFirebase();
  const { toast } = useToast();
  const router = useRouter();

  const [isSaving, setIsSaving] = useState(false);

  const churchDocRef = useMemoFirebase(() => 
    firestore && churchId ? doc(firestore, 'churches', churchId) : null,
    [firestore, churchId]
  );
  const { data: church, isLoading: isLoadingChurch } = useDoc<Church>(churchDocRef);

  const form = useForm<SettingsFormValues>({
    resolver: zodResolver(settingsSchema),
    defaultValues: {
      name: '',
      address: '',
      phone: '',
      adminName: '',
      adminEmail: '',
    },
  });

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (!isAdmin) {
      router.push('/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router]);

  useEffect(() => {
    if (church) {
        form.reset({
            name: church.name || '',
            address: church.address || '',
            phone: church.phone || '',
            adminName: church.adminName || '',
            adminEmail: church.adminEmail || '',
        });
    }
  }, [church, form]);

  const onSubmit = (data: SettingsFormValues) => {
    if (!churchDocRef) return;
    setIsSaving(true);
    
    updateDoc(churchDocRef, data).then(() => {
        toast({
            title: 'Settings Saved',
            description: "Your church's information has been updated.",
        });
    }).catch(async (serverError) => {
        const permissionError = new FirestorePermissionError(appUser, {
            path: churchDocRef.path,
            operation: 'update',
            requestResourceData: data,
        });
        errorEmitter.emit('permission-error', permissionError);
    }).finally(() => {
        setIsSaving(false);
    });
  };
  
  if (!isLoggedIn || !isAdmin || !firestore) {
    return null;
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
          <div className="max-w-3xl mx-auto space-y-6">
            <div>
              <h1 className="text-3xl font-bold font-headline tracking-tight">Church Settings</h1>
              <p className="text-muted-foreground mt-1">
                Manage your church's details and administrator contact information.
              </p>
            </div>

            <Card>
                <CardHeader>
                    <CardTitle>Onboarding</CardTitle>
                    <CardDescription>Need to start over or re-configure your setup?</CardDescription>
                </CardHeader>
                <CardFooter>
                    <Button asChild>
                        <Link href="/admin/setup-wizard">
                            <PenLine className="mr-2 h-4 w-4" /> Run Setup Wizard
                        </Link>
                    </Button>
                </CardFooter>
            </Card>

            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)}>
                <Card>
                  <CardHeader>
                    <CardTitle>Church Information</CardTitle>
                    <CardDescription>This information may be visible to volunteers.</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    {isLoadingChurch ? <Loader2 className="h-6 w-6 animate-spin" /> : (
                      <>
                        <FormField
                          control={form.control}
                          name="name"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Church Name</FormLabel>
                              <FormControl>
                                <Input placeholder="St. Francis' Church" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                        <FormField
                          control={form.control}
                          name="address"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Address</FormLabel>
                              <FormControl>
                                <Input placeholder="123 Gospel Lane" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                        <FormField
                          control={form.control}
                          name="phone"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Phone Number</FormLabel>
                              <FormControl>
                                <Input placeholder="(555) 123-4567" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                      </>
                    )}
                  </CardContent>
                </Card>

                <Card className="mt-6">
                  <CardHeader>
                    <CardTitle>Scheduling Administrator</CardTitle>
                    <CardDescription>The primary contact for volunteer scheduling.</CardDescription>
                  </CardHeader>
                  <CardContent className="grid sm:grid-cols-2 gap-4">
                     {isLoadingChurch ? <Loader2 className="h-6 w-6 animate-spin" /> : (
                      <>
                        <FormField
                          control={form.control}
                          name="adminName"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Admin Name</FormLabel>
                              <FormControl>
                                <Input placeholder="John Smith" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                        <FormField
                          control={form.control}
                          name="adminEmail"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Admin Email</FormLabel>
                              <FormControl>
                                <Input placeholder="admin@example.com" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                      </>
                     )}
                  </CardContent>
                </Card>

                <div className="flex justify-end mt-6">
                  <Button type="submit" disabled={isSaving || isLoadingChurch}>
                    {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}
                    Save Settings
                  </Button>
                </div>
              </form>
            </Form>
          </div>
        </main>
      </div>
    </AppShell>
  );
}
